import React from 'react'
import Layout from 'src/components/Layout'
import Img from 'gatsby-image'
import { graphql } from 'gatsby'
import { Container, Heading, Button } from 'src/components/Base'
import Seo from 'src/components/Seo'

import styles from './404.module.scss'

const NotFoundPage = ({data}) => {

  const images = [data.error];
  const image = images[Math.floor(Math.random() * images.length)];

  return (
    <Layout>
      <Seo title="404: Not found" />
      <Container narrow className={styles.container}>
        <div className={styles.wrapper}>
          <Heading className={styles.heading}>Upps... Seite nicht gefunden</Heading>
          <Img
            fixed={image.childImageSharp.fixed}
          />
          <Button to="/" className={styles.button}>
            Zur Startseite
          </Button>
        </div>
      </Container>
    </Layout>
  )
}



export const query = graphql`
  query ErrorQuery {
    ...image404
    ...image4042
  }
`

export default NotFoundPage
